import axios from 'axios'
import * as globalSelectors from '../global/globalSelectors'
import { carrierError, carrierInfo, isCarrierLoader } from './carrierActions'

axios.defaults.baseURL = 'https://car.busis.eu'

export const getCarrierData = () => async (dispatch, getState) => {
  const auth = globalSelectors.getAuth(getState())
  if (!auth) return
  try {
    const { data } = await axios('/api/user/organisation', {
      headers: { Authorization: auth },
    })
    dispatch(carrierInfo(data))
  } catch (error) {
    dispatch(carrierError(error))
  }
}

export const editCarrier = carrier => async (dispatch, getState) => {
  const auth = globalSelectors.getAuth(getState())
  dispatch(isCarrierLoader())
  if (!auth) return
  try {
    const { data } = await axios.put('api/user/organisation', carrier, {
      headers: { Authorization: auth },
    })
    dispatch(carrierInfo(data))
  } catch (error) {
    dispatch(carrierError(error))
  }
}
