import React, { useState, useEffect, useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Formik, Form } from 'formik'
import * as Yup from 'yup'
import styled from 'styled-components'
import { Button, ButtonRound } from '../stylesheets/mainStyled'
import { ReactComponent as CheckIcon } from '../images/check.svg'
import TextInput from '../components/FormikInputs/TextInput'
import MaskInput from '../components/FormikInputs/MaskInput'
import Loading from '../components/Loader'
import { editCarrier, getCarrierData } from '../redux/carrier/carrierOperations'
import {
  carrierError,
  carrierLoader,
  getCarrierInfo,
} from '../redux/carrier/carrierSelectors'
import { getLang } from '../redux/global/globalSelectors'

const getLabel = lang => {
  switch (lang) {
    case 'RU':
      return 'На русском'
    case 'UA':
      return 'На украинском'
    case 'EN':
      return 'На английском'
    case 'PL':
      return 'На польском'

    default:
      return null
  }
}

const CarrierPage = () => {
  const dispatch = useDispatch()
  const carrier = useSelector(getCarrierInfo)
  const error = useSelector(carrierError)
  const loader = useSelector(carrierLoader)
  const lang = useSelector(getLang).value

  const [isShow, setisShow] = useState(false)

  const getInfo = useCallback(() => {
    dispatch(getCarrierData())
  }, [dispatch])

  useEffect(() => {
    getInfo()
  }, [getInfo])

  const names = ['RU', 'UA', 'EN', 'PL'].filter(el => el !== lang)
  if (error) return <p>Ошибка запроса {error.response.data.status}</p>

  return (
    <>
      {carrier && (
        <Formik
          initialValues={{
            RU: carrier.name.RU,
            UA: carrier.name.UA,
            EN: carrier.name.EN,
            PL: carrier.name.PL,
            phone: carrier.phones[0].replace(/[^0-9]/g, ''),
            email: carrier.emails[0],
            card: carrier.creaditCard,
          }}
          validationSchema={Yup.object({
            RU: isShow || lang === 'RU' ? Yup.string().required('is required') : null,
            UA: isShow || lang === 'UA' ? Yup.string().required('is required') : null,
            EN: isShow || lang === 'EN' ? Yup.string().required('is required') : null,
            PL: isShow || lang === 'PL' ? Yup.string().required('is required') : null,
            phone: Yup.string()
              .test(
                'len',
                'не коректный номер телефона',
                val => val?.replace(/[^0-9]/g, '').length === 12
              )
              .required('is required'),
            email: Yup.string().email('Invalid email address').required('Required'),
            card: Yup.string()
              .test(
                'len',
                'не коректный номер карты',
                val => val?.replace(/[^0-9]/g, '').length === 16
              )
              .required('is required'),
          })}
          onSubmit={async ({ phone, card, email, ...values }) => {
            const name = isShow
              ? { RU: values['RU'], UA: values['UA'], EN: values['EN'], PL: values['PL'] }
              : { ...carrier.name, [lang]: values[lang] }
            const data = {
              name: name,
              phones: [phone.replace(/[^0-9]/g, '')],
              emails: [email],
              creaditCard: card.replace(/[^0-9]/g, ''),
            }
            dispatch(editCarrier(data))
          }}
        >
          <Form>
            <FormBox>
              <TextInput
                label={isShow ? getLabel(lang) : 'Название'}
                name={lang}
                type="text"
              />
              {isShow &&
                names.map(el => (
                  <TextInput key={el} label={getLabel(el)} name={el} type="text" />
                ))}
              <Div>
                <Text>Добавить наименование на других языках?</Text>
                <Button type="button" onClick={() => setisShow(!isShow)}>
                  {isShow ? 'Нет' : 'Да'}
                </Button>
              </Div>
              <MaskInput
                label="Телефон"
                name="phone"
                type="text"
                mask="+38(999)999-99-99"
              />
              <TextInput label="Email" name="email" type="email" />
              <MaskInput
                label="Номер карты для перевода оплат"
                name="card"
                type="text"
                mask="9999-9999-9999-9999"
              />
              <ButtonRound type="submit">
                {loader ? (
                  <Loading type="ThreeDots" color="#00BFFF" height={15} width={40} />
                ) : (
                  <CheckIcon />
                )}
              </ButtonRound>
            </FormBox>
          </Form>
        </Formik>
      )}
    </>
  )
}

export default CarrierPage
const FormBox = styled.div`
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
`
const Div = styled.div`
  display: flex;
  margin-bottom: 12px;
`
const Text = styled.p`
  font-size: 18px;
  margin-right: 12px;
`
